.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.page__body, p, h3 {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.nav {
  display: inline-block;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  list-style: none;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  flex-shrink: 0;
  width: 200px;
  transition: all .3s ease-in-out;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.card__content {
  align-items: center;
  margin: 40px 16px 16px;
}

.card__title {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-weight: 400;
}

.card__price-amount {
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  margin-top: 16px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border-radius: 5px;
}

.card__stars {
  display: flex;
}

.card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars--1 .card__star:nth-child(-n+1), .card__stars--2 .card__star:nth-child(-n+2), .card__stars--3 .card__star:nth-child(-n+3), .card__stars--4 .card__star:nth-child(-n+4), .card__stars--5 .card__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.content {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 0;
  display: grid;
}

@media (width >= 1024px) {
  .content {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .content {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .content {
    grid-template-columns: repeat(2, 200px);
  }
}

